/* Reset CSS */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: 'Segoe UI', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #f2f2f2;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Table Styles */
table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 20px;
}

th, td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f5f5f5;
  font-weight: bold;
}

tr:hover {
  background-color: #f5f5f5;
}

/* Filter Styles */
.filter-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filter-input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  max-width: 300px;
  margin-right: 20px;
}

/* Chart Styles */
.chart-container {
  margin-bottom: 20px;
}

.chart-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

/* Media Queries */
@media (max-width: 767px) {
  .filter-container {
    flex-wrap: wrap;
  }
  
  .filter-input {
    margin-bottom: 10px;
    max-width: 100%;
  }
}
